.Movies {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  width: 300px;
  background-color: red;
  border-radius: 20px;
  color: white;
  text-decoration: none;

  -webkit-box-shadow: red 0 -18px 40px, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  box-shadow: red 0 -18px 40px, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  background: #ecefcf;
  transition: all 0.3s ease-in-out;
}
.Movies:hover {
  -moz-transform: scale(1.2) translate(3px, -50px) skew(7deg, 0deg);
  -webkit-transform: scale(1.2) translate(3px, -50px) skew(7deg, 0deg);
  -o-transform: scale(1.2) translate(3px, -50px) skew(7deg, 0deg);
  -ms-transform: scale(1.2) translate(3px, -50px) skew(7deg, 0deg);
  transform: scale(1.2) translate(3px, -50px) skew(7deg, 0deg);
}
a {
  text-decoration: none;
}
.Movies li {
  list-style: none;
  color: pink;
}
.Movies span {
  color: #ffffff;
  background: #333333;
  text-shadow: 0 -1px 4px #fff, 0 -2px 10px #ff0, 0 -10px 20px #ff8000,
    0 -18px 40px #f00;
}
.Movies li:hover {
  color: black;
}
.Movies img {
  border-radius: 10px;
}
